import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";

import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import changeTitle from "../utils/change-title";
import Select from "../components/FormElements/Select";
import Input from "../components/FormElements/Input";

const CreateSupport = () => {
  const history = useHistory();

  useEffect(() => {
    changeTitle(
      "Create New Support",
      { title: "View Support List", path: "/support-list/0/1" },
      history
    );
  }, [history]);

  return (
    <div>
      <div className="basic-form">
        <form>
          <FormGroup label="Support Category *">
            <Select
              options={[
                { text: "Please select a suitable subcategory", value: "" },
                { text: "Option 2", value: "lorem" },
              ]}
            />
          </FormGroup>

          <FormGroup label="Name *">
            <Input type="name" placeholder="Name" />
          </FormGroup>

          <FormGroup label="Email *">
            <Input type="email" placeholder="Email" />
          </FormGroup>

          <FormGroup label="Phone *">
            <Input type="phone" placeholder="Phone" />
          </FormGroup>

          <FormGroup label="Message *">
            <Input textarea name="message" placeholder="Message" rows="4" />
          </FormGroup>

          <FormGroup>
            <FormButton>Add Support</FormButton>
          </FormGroup>
        </form>
      </div>
    </div>
  );
};

export default CreateSupport;
