import React, { useState, Fragment, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";

import "../shared/js/custom.min.js";
import "../shared/js/deznav-init.js";
import H4 from "../components/Headings/H4.js";
import changeTitle from "../utils/change-title.js";
import Loader from "../components/UI/Loader.js";
import SideBySide from "../components/UI/SideBySide";
import Input from "../components/FormElements/Input.js";

const TicketSupport = () => {
  const [supportIsLoading, setsupportIsLoading] = useState(true);
  const [supportState, setSupportState] = useState([]);
  const [communicationIsLoading, setCommunicationIsLoading] = useState(true);
  const [communicationState, setCommunicationState] = useState([]);
  const [messageState, setMessageState] = useState("");
  const [messageBeingSent, setMessageBeingSent] = useState(false);
  const { supportId } = useParams();

  const messageChangeHandler = (e) => {
    const { value } = e.target;

    setMessageState(value);
  };

  const messangeSendHandler = (e) => {
    e.preventDefault();
    setMessageBeingSent(true);
    axios
      .post("/addsupportcommunication", {
        SupportID: parseInt(supportId),
        AdminYN: false,
        Message: messageState,
        SendEmailYN: false,
      })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setCommunicationState((prevState) => {
          return [...prevState, convertedRes];
        });
      })
      .finally(() => {
        setMessageBeingSent(false);
        setMessageState("");
      });
  };

  useEffect(() => {
    changeTitle("Ticket Support");
  }, []);

  useEffect(() => {
    axios.get(`/getsupport/${supportId}`).then((res) => {
      const convertedRes = JSON.parse(res.data);
      setSupportState(convertedRes);
      setsupportIsLoading(false);
    });
  }, [supportId]);

  useEffect(() => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getsupportcommunication/${supportId}`, {
        cancelToken: source.token,
      })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setCommunicationState(convertedRes);
        setCommunicationIsLoading(false);
      });
  }, [supportId]);

  return (
    <Fragment>
      <section>
        <H4>Category</H4>
        {supportIsLoading ? (
          <Loader />
        ) : (
          <Fragment>
            <SideBySide left="Support No:" right={supportState.SupportID} />
            <SideBySide
              left="Support Category:"
              right={supportState.SupportCategory}
            />
            <SideBySide left="Name:" right={supportState.Name} />
            <SideBySide left="Email:" right={supportState.Email} />
            <SideBySide left="Phone:" right={supportState.Phone} />
          </Fragment>
        )}
      </section>

      <hr />

      <section>
        <H4>Support Communication</H4>
        {communicationIsLoading ? (
          <Loader />
        ) : (
          <div
            className="chatbox mt-5 active"
            style={{
              maxWidth: "700px",
              margin: "auto",
            }}
          >
            <div
              className="card"
              // style={{
              //   padding: "1rem 2rem 0",
              //   borderRadius: 0,
              //   boxShadow: "0 0px 8px 0.16px hsl(233deg 77% 90%)",
              // }}
            >
              <div
                className="card-body msg_card_body dz-scroll ps ps--active-y"
                id="DZ_W_Contacts_Body3"
              >
                {communicationState.map((el) => {
                  return (
                    <div
                      className={`d-flex justify-content-${
                        el.AdminYN ? "start" : "end"
                      } mb-4`}
                    >
                      <div
                        className={`msg_cotainer${!el.AdminYN ? "_send" : ""}`}
                      >
                        <h5>
                          <strong>{el.AdminYN ? "Admin" : "Me"}</strong>
                        </h5>
                        {el.Message}
                        <span
                          className={`msg_time${!el.AdminYN ? "_send" : ""}`}
                        >
                          {new Date(el.AddedDateTime).toLocaleString()}
                        </span>
                      </div>
                    </div>
                  );
                })}
                {/* <div className="d-flex justify-content-end mb-4">
                <div className="msg_cotainer_send">
                  Hi Khalid i am good tnx how about you?
                  <span className="msg_time_send">8:55 AM, Today</span>
                </div>
              </div> */}
              </div>
              <div
                className="card-footer type_msg"
                style={{
                  borderRadius: 0,
                  boxShadow: "0px 0px 30px 0px rgba(82, 63, 105, 0.15)",
                }}
              >
                <div className="input-group">
                  <Input
                    textarea
                    placeholder="Type your message..."
                    value={messageState}
                    onChange={messageChangeHandler}
                  />
                  <div className="input-group-append">
                    <button
                      type="button"
                      className="btn btn-primary ml-4"
                      onClick={messangeSendHandler}
                      disabled={messageBeingSent}
                    >
                      <i className="flaticon-381-send-2"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </section>
    </Fragment>
  );
};

export default TicketSupport;
