import React, { useEffect } from "react";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";

import changeTitle from "../utils/change-title";
import Input from "../components/FormElements/Input";
import H4 from "../components/Headings/H4";
import DataTable from "../components/Tables/DataTable";
import SideBySide from "../components/UI/SideBySide";

const SantaSMS = () => {
  useEffect(() => {
    changeTitle("Santa SMS");
  }, []);

  return (
    <>
      <section>
        <SideBySide left="BroadcastSMS Username:" right="adelaidefamilyshow" />
        <SideBySide left="BroadcastSMS Password:" right="Tony@123" />
        <SideBySide left="Total Contacts:" right="2334" />
      </section>

      <section>
        <div className="basic-form">
          <form>
            <H4>Send Bulk SMS</H4>
            <FormGroup label="Message">
              <Input textarea rows="4" placeholder="Enter message here..." />
            </FormGroup>

            <FormGroup>
              <FormButton>Send Bulk SMS</FormButton>
            </FormGroup>
          </form>
        </div>
      </section>

      <section>
        <DataTable
          heading="Events"
          titles={["Name", "Email", "Mobile", "Address"]}
        >
          {[
            {
              name: "Adriana Petrovic",
              email: "my52ka@adam.com.au",
              mobile: "0421682636",
              address: "3 Warramunga Street, Greenacres, SA, 5086",
            },
            {
              name: "Adriana Petrovic",
              email: "my52ka@adam.com.au",
              mobile: "0421682636",
              address: "3 Warramunga Street, Greenacres, SA, 5086",
            },
            {
              name: "Adriana Petrovic",
              email: "my52ka@adam.com.au",
              mobile: "0421682636",
              address: "3 Warramunga Street, Greenacres, SA, 5086",
            },
          ].map((event, index) => {
            return (
              <tr key={Math.random()}>
                <td>{index + 1}</td>
                <td>{event.name}</td>
                <td>{event.email}</td>
                <td>{event.mobile}</td>
                <td>{event.address}</td>
              </tr>
            );
          })}
        </DataTable>
      </section>
    </>
  );
};

export default SantaSMS;
