import React, { useEffect, useState } from "react";
import axios from "axios";

import { Link } from "react-router-dom";
import DataTable from "../../../components/Tables/DataTable";
import Loader from "../../../components/UI/Loader";

const SupportList = (props) => {
  const { orderId } = props;
  const [supportListIsLoading, setsupportListIsLoading] = useState(true);
  const [supportListState, setSupportListState] = useState([]);

  useEffect(() => {
    axios
      .get(`/getordersupportlist/${orderId}`)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setSupportListState(convertedRes);
        console.log(convertedRes);
      })
      .finally(() => {
        setsupportListIsLoading(false);
      });
  }, [orderId]);

  return (
    <section>
      {supportListIsLoading ? (
        <Loader />
      ) : (
        <DataTable
          heading="Support List"
          titles={["Name", "Email", "Phone", "Action"]}
        >
          {supportListState.map((el, index) => {
            return (
              <tr key={index}>
                <td>{index + 1}</td>
                <td>{el.Name}</td>
                <td>{el.Email}</td>
                <td>{el.Phone}</td>
                <td>
                  <Link
                    to={`/ticket-support/${el.SupportID}`}
                    className="btn btn-primary"
                  >
                    View
                  </Link>
                </td>
              </tr>
            );
          })}
        </DataTable>
      )}
    </section>
  );
};

export default SupportList;
