import React from "react";

const FormGroup = (props) => {
  const { label, fullWidth, children, rootClassName = "" } = props;

  if (fullWidth) {
    return (
      <div className="form-group">
        <label className="mb-1">
          <strong>{label}</strong>
        </label>
        {children}
      </div>
    );
  } else {
    return (
      <div className={`form-group row mx-auto ${rootClassName}`}>
        {label && (
          <label className="col-sm-3 col-form-label font-weight-500">
            {label}
          </label>
        )}
        <div className={`col-sm-5 ${label ? "text-center" : ""}`}>
          <div className="row">
            <div className="col-xl-12">{children}</div>
          </div>
        </div>
      </div>
    );
  }
};

export default FormGroup;
