import React, { useState, useEffect } from "react";
import { Link, useParams, useHistory } from "react-router-dom";
import axios from "axios";
import $ from "jquery";

import changeTitle from "../utils/change-title";
import DataTable from "../components/Tables/DataTable";
import TableActions from "../components/Buttons/TableActions";
import Loader from "../components/UI/Loader";
import H4 from "../components/Headings/H4";

const Orders = () => {
  const [statuses] = useState([
    "Inactive Orders",
    "Active Orders",
    "Refunded Orders",
    "System Refunded Orders",
    "All Orders",
  ]);
  const { sessionId = 0, status = 0 } = useParams();
  const [ordersState, setOrdersState] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const history = useHistory();

  const dropdownSelectHandler = (e) => {
    const { value } = e.target;

    history.replace(`/orders/${sessionId}/${value}`);
  };

  useEffect(() => {
    setIsLoading(true);
    let URL = "/getorderslist";
    let reqParams = {
      SessionID: parseInt(sessionId),
      Status: parseInt(status),
    };
    if (sessionId === "duplicate-orders") {
      URL = "/getduplicateorderslist";
      reqParams = { Status: parseInt(status) };
    }
    axios
      .post(URL, reqParams)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setOrdersState(convertedRes);
      })
      .finally(() => {
        setIsLoading(false);
      });
    return () => {};
  }, [sessionId, status]);

  useEffect(() => {
    changeTitle("Orders");

    $("#ordersPageLink, #ordersPageLink > a").addClass("mm-active");

    console.log($("#ordersPageLink, #ordersPageLink > a"));

    return () => {
      $("#ordersPageLink").removeClass("mm-active");
    };
  }, [history]);

  return (
    <div className="container-fluid p-3">
      <section>
        <H4>Status</H4>
        <div className="">
          <div className="" style={{ maxWidth: "320px" }}>
            {/* <div className="d-flex justify-content-center align-items-center my-3 flex-wrap"> */}
            <select
              className="form-control my-4"
              value={status}
              onChange={dropdownSelectHandler}
            >
              {statuses.map((el, index) => {
                return (
                  <option key={index} value={index}>
                    {el}
                  </option>
                  // <button
                  //   key={index}
                  //   className={`btn btn-outline-primary my-2 mx-3 ${
                  //     status.toString() === index.toString() ? "active" : ""
                  //   }`}
                  //   onClick={() =>
                  //     // history.replace(`/proxy-comp/orders/${sessionId}/${index}`)
                  //     history.replace(`/orders/${sessionId}/${index}`)
                  //   }
                  // >
                  //   {el}
                  // </button>
                );
              })}
            </select>
          </div>
        </div>
      </section>

      {isLoading ? (
        <Loader />
      ) : (
        <DataTable
          heading={statuses[status]}
          titles={["Order No", "Date", "Name", "Email", "Phone", "Actions"]}
        >
          {ordersState.map((order, index) => {
            return (
              <tr key={Math.random()}>
                <td>{index + 1}</td>
                <td>
                  <Link
                    className="text-primary"
                    to={`/order-details/${order.OrderNo}`}
                  >
                    {order.OrderNo}
                  </Link>
                </td>
                <td>{new Date(order.OrderDate).toLocaleString()}</td>
                <td>{order.ContactName}</td>
                <td>{order.ContactEmail}</td>
                <td>{order.ContactPhone}</td>
                <td>
                  <TableActions>
                    {sessionId === "duplicate-orders" ? (
                      <>
                        <Link
                          to={`/order-details/${order.OrderID}`}
                          className="dropdown-item"
                        >
                          Make Inactive
                        </Link>
                        <Link
                          to={`/sms-log/${order.SessionID}`}
                          className="dropdown-item"
                        >
                          Refund
                        </Link>
                      </>
                    ) : (
                      <>
                        <Link to={`/admin`} className="dropdown-item">
                          Admin
                        </Link>
                        <Link
                          to={`/order-details/${order.OrderID}`}
                          className="dropdown-item"
                        >
                          Order Details
                        </Link>
                        <Link
                          to={`/admin-order/${order.OrderID}`}
                          className="dropdown-item"
                        >
                          Admin Order
                        </Link>
                        <Link to={`/email/${""}`} className="dropdown-item">
                          Send Email
                        </Link>
                      </>
                    )}
                  </TableActions>
                </td>
              </tr>
            );
          })}
        </DataTable>
      )}
    </div>
  );
};

export default Orders;
