import React, { useEffect, useState } from "react";
import { useHistory, useLocation } from "react-router-dom";
import axios from "axios";

import changeTitle from "../utils/change-title";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";
import Breadcrumb from "../components/UI/Breadcrumb";
import decodeQueryParams from "../utils/decode-query-qarams";
import isEmpty from "../utils/is-empty";
import AlertSuccess from "../components/Alert/AlertSuccess";

const CreateEvent = () => {
  const history = useHistory();
  const [isBeingProcessed, setIsBeingProcessed] = useState(false);
  const [eventState, setEventState] = useState({
    EventID: "",
    EventName: "",
    EventDescription: "",
    EventLocation: "",
    EventAddress: "",
    EventMap: "",
  });
  const location = useLocation();
  const [responseMessage, setResponseMessage] = useState("");

  const inputChangeHandler = (e) => {
    const { name, value } = e.target;

    setEventState((prevState) => {
      return {
        ...prevState,
        [name]: value,
      };
    });
  };

  const eventSubmitHandler = (e) => {
    e.preventDefault();

    console.log(eventState);
    setIsBeingProcessed(true);
    setResponseMessage(false);
    axios
      .post("/updateevent", eventState)
      .then((res) => {
        setResponseMessage(false);
        if (!isEmpty(JSON.parse(res.data).EventID)) {
          setResponseMessage("Event updated successfully");
        }
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        setIsBeingProcessed(false);
      });
  };

  useEffect(() => {
    changeTitle(
      "Update Event",
      { title: "View Events List", path: "/events" },
      history
    );

    const { search } = location;
    const {
      EventID,
      EventName,
      EventDescription,
      EventLocation,
      EventAddress,
      EventMap,
    } = decodeQueryParams(search);
    setEventState({
      EventID,
      EventName,
      EventDescription,
      EventLocation,
      EventAddress,
      EventMap,
    });
    if (isEmpty(EventID)) {
      history.push("/events");
    }
  }, [history, location]);

  console.log(decodeQueryParams(eventState));

  return (
    <>
      <Breadcrumb
        showEventName
        items={[{ title: "Events", path: "/events" }, "Update Event"]}
      />
      {responseMessage && <AlertSuccess>{responseMessage}</AlertSuccess>}
      <div className="basic-form">
        <form onSubmit={eventSubmitHandler}>
          <FormGroup label="Event Name *">
            <Input
              type="text"
              name="EventName"
              value={eventState.EventName}
              onChange={inputChangeHandler}
              placeholder="Event Name"
            />
          </FormGroup>
          <FormGroup label="Event Description *">
            <Input
              textarea
              name="EventDescription"
              value={eventState.EventDescription}
              onChange={inputChangeHandler}
              placeholder="Event Description *"
              rows="4"
            />
          </FormGroup>
          {/* <FormGroup label="Start Date/Time *">
            <Input type="datetime-local" name="daterange" />
          </FormGroup>
          <FormGroup label="End Date/Time *">
            <Input type="datetime-local" name="daterange" />
          </FormGroup> */}
          <FormGroup label="Event Location">
            <Input
              type="text"
              name="EventLocation"
              value={eventState.EventLocation}
              onChange={inputChangeHandler}
              placeholder="Event Location"
            />
          </FormGroup>
          <FormGroup label="Event Address">
            <Input
              type="text"
              name="EventAddress"
              value={eventState.EventAddress}
              onChange={inputChangeHandler}
              placeholder="Event Address"
            />
          </FormGroup>
          <FormGroup label="Event Map">
            <Input
              type="text"
              name="EventMap"
              value={eventState.EventMap}
              onChange={inputChangeHandler}
              placeholder="Event Map"
            />
          </FormGroup>

          <FormGroup>
            <FormButton disabled={isBeingProcessed}>Update</FormButton>
          </FormGroup>
        </form>
      </div>
    </>
  );
};

export default CreateEvent;
