import React, { useEffect, Fragment, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";

import H4 from "../components/Headings/H4";
import DataTable from "../components/Tables/DataTable";
import Loader from "../components/UI/Loader";
import SideBySide from "../components/UI/SideBySide";
import changeTitle from "../utils/change-title";
import FormGroup from "../components/FormElements/FormGroup";
import FormButton from "../components/Buttons/FormButton";
import Input from "../components/FormElements/Input";

const OrderDetails = () => {
  const [orderState, setOrderState] = useState([]);
  const [attendeeState, setAttendeeState] = useState([]);
  const [orderItemState, setOrderItemState] = useState([]);
  const [attendeeIsLoading, setAttendeeIsLoading] = useState(true);
  const [orderIsLoading, setOrderIsLoading] = useState(true);
  const [orderItemIsLoading, setOrderItemIsLoading] = useState(true);
  const [attendeeBeingUpdate, setAttendeeBeingUpdate] = useState(false);
  const { orderId } = useParams();

  const attendeeChangeHandler = (id, e) => {
    const { value } = e.target;

    const values = [...attendeeState];
    const indexToChange = values.findIndex(
      (el) => el.AttendeeID.toString() === id.toString()
    );
    values[indexToChange].Name = value;
    setAttendeeState(values);
  };

  const attendeeUpdateHandler = (e) => {
    e.preventDefault();

    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    setAttendeeBeingUpdate(true);
    axios
      .post("/updateattendeelist", attendeeState, { cancelToken: source.token })
      .then(() => {
        setAttendeeBeingUpdate(false);
      })
      .finally(() => source.cancel());
  };

  useEffect(() => {
    changeTitle("Order Details");
  }, []);

  useEffect(() => {
    setAttendeeIsLoading(true);
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getattendeelist/${orderId}`, {
        cancelToken: source.token,
      })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        const mappedResponse = convertedRes.map((el) => {
          return {
            AttendeeID: el.AttendeeID,
            Name: el.Name,
          };
        });
        setAttendeeState(mappedResponse);
        setAttendeeIsLoading(false);
      })
      .finally(() => source.cancel());
  }, [orderId]);

  useEffect(() => {
    setOrderIsLoading(true);
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getorder/${orderId}`, {
        cancelToken: source.token,
      })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setOrderState(convertedRes);
        setOrderIsLoading(false);
      })
      .finally(() => source.cancel());
  }, [orderId]);

  useEffect(() => {
    setOrderItemIsLoading(true);
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getorderitem/${orderId}`, {
        cancelToken: source.token,
      })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setOrderItemState(convertedRes);
        setOrderItemIsLoading(false);
      })
      .finally(() => source.cancel());
  }, [orderId]);

  return (
    <Fragment>
      {orderIsLoading ? (
        <Loader />
      ) : (
        <section>
          <H4>Order Details</H4>
          <SideBySide left="Event Name:" right={orderState.EventName} />
          <SideBySide
            left="Session:"
            right={`
              ${new Date(
                orderState.SessionStartDateTime
              ).toLocaleString()} - ${new Date(
              orderState.SessionEndDateTime
            ).toLocaleString()}
            `}
          />
          <SideBySide left="Event Address" right={orderState.EventAddress} />
          <SideBySide left="Order No:" right={orderState.OrderNo} />
          <SideBySide
            left="Date:"
            right={new Date(orderState.OrderDate).toLocaleString()}
          />
          <SideBySide left="Name:" right={orderState.ContactName} />
          <SideBySide left="Email:" right={orderState.ContactEmail} />
          <SideBySide left="Phone:" right={orderState.ContactPhone} />
        </section>
      )}

      <hr />

      {orderItemIsLoading ? (
        <Loader />
      ) : (
        <section>
          <DataTable
            paging={false}
            heading="Order Item Details"
            titles={["Order Name", "Price", "Quantity", "Total"]}
          >
            {/* {orderItemState.map((orderItem, index) => {
              return ( */}
            <tr key={Math.random()}>
              <td>{1}</td>
              <td>{orderItemState.EventItemName}</td>
              <td>{orderItemState.EventItemPrice}</td>
              <td>{orderItemState.Quantity}</td>
              <td>{orderItemState.TotalPrice}</td>
            </tr>
            {/*   );
            })} */}
          </DataTable>
          <SideBySide left="Sub Total:" right={"$" + orderState.SubTotal} />
          {orderState.CashYN === false && (
            <SideBySide left="Booking Fee:" right="$1.99" />
          )}
          {orderState.DiscountAmount > 0 && (
            <SideBySide
              left="Discount Amount:"
              right={"$" + orderState.DiscountAmount}
            />
          )}
          <SideBySide
            left="Total Amount:"
            right={"$" + orderState.TotalAmount}
          />
          <SideBySide left="Paid Amount:" right={"$" + orderState.PaidAmount} />
        </section>
      )}

      <hr />

      {attendeeIsLoading ? (
        <Loader />
      ) : (
        <section>
          <H4>Attendee Details</H4>

          <div className="basic-form">
            <form>
              {attendeeState.map((el, index) => {
                return (
                  <FormGroup
                    key={el.AttendeeID}
                    label={`Attendee ${index + 1}`}
                  >
                    <Input
                      type="text"
                      value={el.Name}
                      onChange={(e) => attendeeChangeHandler(el.AttendeeID, e)}
                      placeholder="Attendee Name"
                    />
                  </FormGroup>
                );
              })}
              <FormGroup>
                <FormButton
                  onClick={attendeeUpdateHandler}
                  disabled={attendeeBeingUpdate}
                >
                  Update
                </FormButton>
              </FormGroup>
            </form>
          </div>
        </section>
      )}
    </Fragment>
  );
};

export default OrderDetails;

