import React, { useEffect, useState } from "react";
import axios from "axios";

import DataTable from "../../../components/Tables/DataTable";
import Loader from "../../../components/UI/Loader";

const TicketsLeft = (props) => {
  const { orderId } = props;
  const [ticketLeftState, setTicketLeftState] = useState([]);
  const [ticketLeftIsLoading, setTicketLeftIsLoading] = useState(true);

  useEffect(() => {
    axios
      .get(`/getticketleft/${orderId}`)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setTicketLeftState(convertedRes);
        console.log(convertedRes);
      })
      .finally(() => {
        setTicketLeftIsLoading(false);
      });
  }, [orderId]);

  return (
    <section>
      {ticketLeftIsLoading ? (
        <Loader />
      ) : (
        <DataTable
          heading="Tickets Left"
          titles={["Name", "Total Quantity", "Left Quantity"]}
        >
          {ticketLeftState.map((el, index) => {
            return (
              <tr key={index}>
                <td>{index + 1}</td>
                <td>{el.EventItemName}</td>
                <td>{el.TotalQty}</td>
                <td>{el.LeftQty}</td>
              </tr>
            );
          })}
        </DataTable>
      )}
    </section>
  );
};

export default TicketsLeft;
