import React, { useState, useEffect } from "react";
import axios from "axios";

import FormButton from "../../../components/Buttons/FormButton";
import FormGroup from "../../../components/FormElements/FormGroup";
import Input from "../../../components/FormElements/Input";
import DataTable from "../../../components/Tables/DataTable";
import Loader from "../../../components/UI/Loader";

const Attendees = (props) => {
  const { orderId } = props;

  const [attendeeIsLoading, setAttendeeIsLoading] = useState(true);
  const [attendeeState, setAttendeeState] = useState([]);
  const [attendeeInput, setAttendeeInput] = useState({
    OrderID: orderId,
    Name: "",
  });

  const attendeeInputChangeHandler = (e) => {
    setAttendeeInput((prevState) => {
      return { ...prevState, Name: e.target.value };
    });
  };

  const addAttendeeHandler = (e) => {
    e.preventDefault();
    setAttendeeIsLoading(true);
    console.log(attendeeInput);
    axios
      .post("/addattendee", attendeeInput)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setAttendeeState([...attendeeState, convertedRes]);
      })
      .finally(() => {
        setAttendeeInput((prevState) => {
          return { ...prevState, Name: "" };
        });
        setAttendeeIsLoading(false);
      });
  };

  useEffect(() => {
    setAttendeeIsLoading(true);
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getattendeelist/${orderId}`, {
        cancelToken: source.token,
      })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        const mappedResponse = convertedRes.map((el) => {
          return {
            AttendeeID: el.AttendeeID,
            Name: el.Name,
          };
        });
        setAttendeeState(mappedResponse);
      })
      .finally(() => {
        setAttendeeIsLoading(false);
        source.cancel();
      });
  }, [orderId]);

  return (
    <section>
      {attendeeIsLoading ? (
        <Loader />
      ) : (
        <DataTable heading="Attendees" titles={["Attendee"]}>
          {attendeeState.map((el, index) => {
            return (
              <tr key={index}>
                <td>{index + 1}</td>
                <td>{el.Name}</td>
              </tr>
            );
          })}
        </DataTable>
      )}

      <div className="basic-form">
        <form onSubmit={addAttendeeHandler}>
          <FormGroup label="Companion Name *">
            <Input
              type="text"
              name="Name"
              placeholder="Enter Name"
              value={attendeeInput.Name}
              onChange={attendeeInputChangeHandler}
            />
          </FormGroup>

          <FormGroup>
            <FormButton>Add Companion</FormButton>
          </FormGroup>
        </form>
      </div>
    </section>
  );
};

export default Attendees;
