import updateObject from "../../utils/update-object";
import * as actionTypes from "../actions/actionTypes";

const initialState = {
  currentEventName: "",
};

const setCurrentEvent = (state, action) => {
  return updateObject(state, { currentEventName: action.eventName });
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_CURRENT_EVENT:
      return setCurrentEvent(state, action);
    default:
      return state;
  }
};

export default reducer;
