import React, { useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import TableActions from "../components/Buttons/TableActions";

import DataTable from "../components/Tables/DataTable";
import changeTitle from "../utils/change-title";

const ShowBagCouponsList = () => {
  const history = useHistory();

  useEffect(() => {
    changeTitle(
      "Show Bags Coupon List",
      { title: "Create Show Bag Coupon", path: "/create-coupon" },
      history
    );
  }, [history]);

  return (
    <>
      <section>
        <DataTable
          heading="Events"
          titles={[
            "Coupon Code",
            "Discount Percentage",
            "Discount Amount",
            "Actions",
          ]}
        >
          {[
            {
              code: "FLAT300",
              discountAmount: "25.00",
              discountPercentage: "25.00",
            },
          ].map((coupon, index) => {
            return (
              <tr key={Math.random()}>
                <td>{index + 1}</td>
                <td>{coupon.code}</td>
                <td>${coupon.discountPercentage}</td>
                <td>{coupon.discountAmount}</td>
                <td>
                  <TableActions>
                    <Link
                      to={`/price-list/${coupon.EventID}`}
                      className="dropdown-item"
                    >
                      Delete
                    </Link>
                  </TableActions>
                </td>
              </tr>
            );
          })}
        </DataTable>
      </section>
    </>
  );
};

export default ShowBagCouponsList;
