import React, { useState, useCallback, useEffect } from "react";
import { useParams, useHistory, Link } from "react-router-dom";
import axios from "axios";

import changeTitle from "../utils/change-title";
import DataTable from "../components/Tables/DataTable";
import TableActions from "../components/Buttons/TableActions";
import Loader from "../components/UI/Loader";
import Breadcrumb from "../components/UI/Breadcrumb";
import AlertSuccess from "../components/Alert/AlertSuccess";

const Coupons = () => {
  const [couponsState, setCouponsState] = useState([]);
  const { eventId } = useParams();
  const [isLoading, setIsLoading] = useState(true);
  const [responseMessage, setResponseMessage] = useState("");
  const history = useHistory();

  const couponDeleteHandler = (e, couponId) => {
    e.preventDefault();

    setResponseMessage(false);
    axios.get(`/deleteeventcoupon/${couponId}`).then((res) => {
      console.log(JSON.parse(res.data));
      setResponseMessage("Coupon deleted successfully!");
      fetchCoupons();
    });
  };

  const fetchCoupons = useCallback(() => {
    setIsLoading(true);
    axios
      .get(`/geteventcouponslist/${eventId}`)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        console.log(convertedRes);
        setCouponsState(convertedRes);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }, [eventId]);

  useEffect(() => {
    changeTitle(
      "Coupon",
      { title: "Create Coupon", path: `/create-coupon/${eventId}` },
      history
    );
    fetchCoupons();
  }, [eventId, history, fetchCoupons]);

  return (
    <>
      <Breadcrumb
        showEventName
        items={[{ title: "Events", path: "/events/" }, "Coupons"]}
      />
      {responseMessage && <AlertSuccess>{responseMessage}</AlertSuccess>}
      {isLoading ? (
        <Loader />
      ) : (
        <DataTable
          heading="Price List"
          titles={[
            "Coupon Code",
            "Discount Percentage",
            "Discount Amount",
            "Actions",
          ]}
        >
          {couponsState.map((item, index) => {
            const editURI = `?EventCouponID=${decodeURIComponent(
              item.EventCouponID
            )}&Code=${item.Code}&DiscountPercentage=${decodeURIComponent(
              item.DiscountPercentage
            )}&DiscountAmount=${decodeURIComponent(
              item.DiscountAmount
            )}&eventId=${eventId}`;
            return (
              <tr key={item.EventCouponID}>
                <td>{index + 1}</td>
                <td>{item.Code}</td>
                <td>{item.DiscountPercentage}</td>
                <td>{item.DiscountAmount}</td>
                <td>
                  <TableActions>
                    <Link
                      to={`/edit-coupon${editURI}`}
                      className="dropdown-item"
                    >
                      Edit
                    </Link>
                    <button
                      onClick={(e) =>
                        couponDeleteHandler(e, item.EventCouponID)
                      }
                      className="dropdown-item"
                    >
                      Delete
                    </button>
                  </TableActions>
                </td>
              </tr>
            );
          })}
        </DataTable>
      )}
    </>
  );
};

export default Coupons;
