import $ from "jquery";

const changeTitle = (title, togglerButtonData, history) => {
  document.getElementById("dynamic-title").innerText = title;
  document.title = title;
  window.scrollTo(0, 0);
  $("#event-list-toggler").addClass("hide");
  if (togglerButtonData && history) {
    $("#event-list-toggler")
      .html(togglerButtonData.title)
      .removeClass("hide")
      .off()
      .click(() => {
        history.push(togglerButtonData.path);
      });
  }
};

export default changeTitle;
