import React, { useEffect } from "react";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";
import DataTable from "../components/Tables/DataTable";

import changeTitle from "../utils/change-title";

const VoucherSettings = () => {
  useEffect(() => {
    changeTitle("Voucher Settings");
  }, []);

  return (
    <>
      <div className="basic-form">
        <form>
          <FormGroup label="Validity Days">
            <Input type="number" step="1" placeholder="Days" />
          </FormGroup>

          <FormGroup>
            <FormButton>Update</FormButton>
          </FormGroup>
        </form>
      </div>

      <hr />

      <div className="basic-form">
        <form>
          <FormGroup label="Amount">
            <Input type="number" step="1" placeholder="Add Package" />
          </FormGroup>

          <FormGroup>
            <FormButton>Add Package</FormButton>
          </FormGroup>
        </form>
      </div>

      <hr />

      <section>
        <DataTable heading="Voucher Packages" titles={["Amount"]}>
          {[
            {
              amount: "54",
            },
            {
              amount: "54",
            },
            {
              amount: "54",
            },
            {
              amount: "54",
            },
            {
              amount: "54",
            },
            {
              amount: "54",
            },
          ].map((el, index) => {
            return (
              <tr>
                <td>{index + 1}</td>
                <td>${el.amount}</td>
              </tr>
            );
          })}
        </DataTable>
      </section>
    </>
  );
};

export default VoucherSettings;
