import React, { useState, useEffect } from "react";
import { useParams, useHistory } from "react-router-dom";
import axios from "axios";
import { Link } from "react-router-dom";

import changeTitle from "../utils/change-title";
import TableActions from "../components/Buttons/TableActions";
import DataTable from "../components/Tables/DataTable";
import Loader from "../components/UI/Loader";
import Breadcrumb from "../components/UI/Breadcrumb";

const Sessions = () => {
  const { eventId } = useParams();
  const [sessionsState, setSessionsState] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  // const eventName = "Adelaide Family Show";
  const history = useHistory();

  useEffect(() => {
    axios
      .post(`/geteventsessionslist/`, {
        eventID: eventId,
        SessionHideYN: false,
      })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setSessionsState(convertedRes);
        setIsLoading(false);
      });
    return () => {};
  }, [eventId]);

  useEffect(() => {
    changeTitle(
      "Sessions",
      { title: "Create New Session", path: `/create-session/${eventId}` },
      history
    );
  }, [history, eventId]);

  return (
    <>
      <Breadcrumb
        showEventName
        items={[{ title: "Events", path: `/events/${eventId}` }, "Sessions"]}
      />
      <div className="container-fluid p-3 pt-0">
        {isLoading ? (
          <Loader />
        ) : (
          <DataTable
            titles={[
              "Start Date/Time",
              "End Date/Time",
              "Bookings",
              "Total",
              "Attended",
              "Unattended",
              "Action",
            ]}
          >
            {sessionsState.map((session, index) => {
              const query = `SessionID=${session.SessionID}&SessionStartDateTime=${session.SessionStartDateTime}&SessionEndDateTime=${session.SessionEndDateTime}`;

              return (
                <tr key={Math.random()}>
                  <td>{index + 1}</td>
                  <td>
                    {new Date(session.SessionStartDateTime).toLocaleString()}
                  </td>
                  <td>
                    {new Date(session.SessionEndDateTime).toLocaleString()}
                  </td>
                  <td>{session.Bookings}</td>
                  <td>{session.Total}</td>
                  <td>{session.Attended}</td>
                  <td>{session.Unattended}</td>
                  <td>
                    <TableActions>
                      <Link
                        to={`/edit-session/${eventId}?${query}`}
                        className="dropdown-item"
                      >
                        Edit
                      </Link>
                      <Link
                        to={`/orders/${session.SessionID}/0`}
                        className="dropdown-item"
                      >
                        Orders
                      </Link>
                      <Link
                        to={`/bulk-sms/${eventId}/${session.SessionID}`}
                        className="dropdown-item"
                      >
                        Bulk SMS
                      </Link>
                      <Link
                        to={`/bulk-email/${eventId}/${session.SessionID}`}
                        className="dropdown-item"
                      >
                        Bulk Email
                      </Link>
                    </TableActions>
                  </td>
                </tr>
              );
            })}
          </DataTable>
        )}
      </div>
    </>
  );
};

export default Sessions;
