import React, { useEffect, useState } from "react";
import axios from "axios";
import Loader from "../../../components/UI/Loader";
import DataTable from "../../../components/Tables/DataTable";
import Input from "../../../components/FormElements/Input";
import FormGroup from "../../../components/FormElements/FormGroup";
import FormButton from "../../../components/Buttons/FormButton";
import isEmpty from "../../../utils/is-empty";

const SMSDetails = (props) => {
  const { URL, orderId } = props;
  const [orderSMSIsLoading, setOrderSMSIsLoading] = useState(true);
  const [orderSMSState, setOrderSMSState] = useState([]);
  const [smsFormState, setSmsFormState] = useState({
    OrderID: orderId,
    Message: "",
  });
  const [smsBeingProcessed, setSmsBeingProcessed] = useState(false);
  const [urlState, setUrlState] = useState(URL);
  const [urlBeingProcessed, seturlBeingProcessed] = useState(false);

  const smsChangeHandler = (e) => {
    setSmsFormState((prevState) => {
      return {
        ...prevState,
        Message: e.target.value,
      };
    });
  };

  const smsSendHandler = (e) => {
    e.preventDefault();
    setSmsBeingProcessed(true);
    console.log(smsFormState);

    axios
      .post("/sendordersms", smsFormState)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        console.log(convertedRes);
      })
      .finally(() => {
        setSmsBeingProcessed(false);
      });
  };

  const urlGenerateHandler = () => {
    seturlBeingProcessed(true);
    axios
      .get(`/generateorderlink/${orderId}`)
      .then((res) => {
        console.log(res);
        const convertedRes = JSON.parse(res.data);
        console.log(convertedRes);
        setUrlState(convertedRes.BitlyURL);
      })
      .finally(() => {
        seturlBeingProcessed(false);
      });
  };

  useEffect(() => {
    axios
      .get(`/getordersms/${orderId}`)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setOrderSMSState(convertedRes);
        console.log(convertedRes);
      })
      .finally(() => {
        setOrderSMSIsLoading(false);
      });
  }, [orderId]);
  return (
    <section>
      {orderSMSIsLoading ? (
        <Loader />
      ) : (
        <DataTable heading="SMS Details" titles={["Date", "SMS"]}>
          {orderSMSState.map((el, index) => {
            return (
              <tr key={el.SMSLogID}>
                <td>{index + 1}</td>
                <td>{new Date(el.AddedDate).toLocaleString()}</td>
                <td dangerouslySetInnerHTML={{ __html: el.Message }}></td>
              </tr>
            );
          })}
        </DataTable>
      )}

      <hr />
      <FormGroup label="Bitly URL:">
        {!isEmpty(urlState) ? urlState : "Not created"}
      </FormGroup>
      {isEmpty(urlState) && (
        <FormGroup>
          <FormButton disabled={urlBeingProcessed} onClick={urlGenerateHandler}>
            Generate
          </FormButton>
        </FormGroup>
      )}
      <hr />
      <form onSubmit={smsSendHandler}>
        <FormGroup label="Send SMS">
          <Input
            textarea
            rows="4"
            name="Message"
            value={smsFormState.Message}
            onChange={smsChangeHandler}
            placeholder="Enter message here..."
          />
        </FormGroup>
        <FormGroup>
          <FormButton disabled={smsBeingProcessed}>Send</FormButton>
        </FormGroup>
      </form>
      {/* <hr /> */}
    </section>
  );
};

export default SMSDetails;
