import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import axios from "axios";

import changeTitle from "../utils/change-title";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";
import Select from "../components/FormElements/Select";
import Breadcrumb from "../components/UI/Breadcrumb";

const CreateCoupon = () => {
  const history = useHistory();
  const { eventId } = useParams();
  const [couponState, setCouponState] = useState({
    EventID: eventId,
    Code: "",
    DiscountPercentage: "",
    DiscountAmount: "",
  });
  const [isBeingProcessed, setIsBeingProcessed] = useState(false);
  const [type, setType] = useState("");

  const typeTogglerHandler = (e) => {
    const { value } = e.target;
    setType(value);
    let key = "DiscountAmount";
    if (value === "Amount") {
      key = "DiscountPercentage";
    }

    setCouponState((prevState) => {
      return {
        ...prevState,
        [key]: null,
      };
    });
  };

  const inputChangeHandler = (e) => {
    const { name, value } = e.target;

    setCouponState((prevState) => {
      return {
        ...prevState,
        [name]: value,
      };
    });
  };

  const formSubmitHandler = (e) => {
    e.preventDefault();

    setIsBeingProcessed(true);
    axios
      .post("/createeventcoupon", couponState)
      .then((res) => {
        setCouponState({
          EventID: eventId,
          Code: "",
          DiscountPercentage: "",
          DiscountAmount: "",
        });
      })
      .catch((err) => {})
      .finally(() => {
        setIsBeingProcessed(false);
      });

    console.log(couponState);
  };

  useEffect(() => {
    changeTitle(
      "Create New Coupon",
      { title: "View Coupons List", path: `/coupons/${eventId}` },
      history
    );
  }, [history, eventId]);

  return (
    <>
      <Breadcrumb
        showEventName
        items={[
          { title: "Events", path: "/event" },
          { title: "Coupons", path: `/coupons/${eventId}` },
          "Create New Coupon",
        ]}
      />
      <div className="basic-form">
        <form onSubmit={formSubmitHandler}>
          <FormGroup label="Coupon Code">
            <Input
              type="text"
              name="Code"
              placeholder="Coupon Code"
              value={couponState.Code}
              onChange={inputChangeHandler}
            />
          </FormGroup>

          <FormGroup label="Coupon type">
            <Select
              name="type"
              value={type}
              onChange={typeTogglerHandler}
              options={[
                { text: "Please select", value: "" },
                {
                  text: "Percentage",
                  value: "Percentage",
                },
                { text: "Amount", value: "Amount" },
              ]}
            ></Select>
          </FormGroup>

          {type === "Amount" && (
            <FormGroup label="Amount">
              <Input
                type="number"
                name="DiscountAmount"
                placeholder="Amount"
                value={couponState.DiscountAmount}
                onChange={inputChangeHandler}
              />
            </FormGroup>
          )}

          {type === "Percentage" && (
            <FormGroup label="Percentage">
              <Input
                type="number"
                name="DiscountPercentage"
                placeholder="Percentage"
                value={couponState.DiscountPercentage}
                onChange={inputChangeHandler}
              />
            </FormGroup>
          )}

          <FormGroup>
            <FormButton disabled={isBeingProcessed}>Add Coupon</FormButton>
          </FormGroup>
        </form>
      </div>
    </>
  );
};

export default CreateCoupon;
