import React, { useEffect } from "react";

import changeTitle from "../utils/change-title";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";
import H4 from "../components/Headings/H4";
import DataTable from "../components/Tables/DataTable";
import SideBySide from "../components/UI/SideBySide";

const AdminViewOrder = () => {
  useEffect(() => {
    changeTitle("Admin View Order");
  }, []);
  return (
    <>
      <section>
        <DataTable
          heading="Tickets Left"
          titles={["Type", "Left", "Attendees"]}
        >
          {[
            {
              type: "Single Entry",
              left: "Single Entry",
              attendees: 1,
            },
          ].map((el, index) => {
            return (
              <tr>
                <td>{index + 1}</td>
                <td>{el.type}</td>
                <td>{el.left}</td>
                <td>{el.attendees}</td>
              </tr>
            );
          })}
        </DataTable>
        <button className="btn btn-primary mx-2 mt-3">Add Attendee</button>
        <button className="btn btn-primary mx-2 mt-3">Back To Home</button>
        <button className="btn btn-primary mx-2 mt-3">Scan</button>
      </section>

      <hr />

      <section>
        <H4>Tickets Logged</H4>
      </section>

      <hr />

      <section>
        <H4>Order Details</H4>

        <SideBySide left="Event Name:" right="Adelaide Family Show" />
        <SideBySide left="Session:" right="07/11/2020 06:00 PM - 09:00 PM" />
        <SideBySide
          left="Event Location:"
          right="Edwards Park, Anzac Hwy, Adelaide SA 5000"
        />
        <SideBySide left="Order No:" right="123465" />
        <SideBySide left="Date:" right="03/11/2020" />
      </section>

      <hr />

      <section>
        <div className="basic-form">
          <form>
            <FormGroup label="4 digits number">
              <Input type="name" placeholder="4 digits number" />
            </FormGroup>

            <FormGroup>
              <FormButton>Submit</FormButton>
            </FormGroup>
          </form>
        </div>
      </section>
    </>
  );
};

export default AdminViewOrder;
