import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import axios from "axios";

import changeTitle from "../utils/change-title";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";
import Breadcrumb from "../components/UI/Breadcrumb";

const CreateEventItem = () => {
  const history = useHistory();
  const { eventId } = useParams();
  const [isBeingProcessed, setIsBeingProcessed] = useState(false);
  const [itemState, setItemState] = useState({
    EventID: eventId,
    EventItemName: "",
    EventItemPrice: "",
  });

  const inputChangeHandler = (e) => {
    const { name, value } = e.target;

    setItemState((prevState) => {
      return { ...prevState, [name]: value };
    });
  };

  const formSubmitHandler = (e) => {
    e.preventDefault();
    console.log(itemState);
    setIsBeingProcessed(true);
    axios
      .post("/createeventitem", itemState)
      .then((res) => {
        setItemState({
          EventID: eventId,
          EventItemName: "",
          EventItemPrice: "",
        });
      })
      .catch((err) => console.log(err))
      .finally(() => {
        setIsBeingProcessed(false);
      });
  };

  useEffect(() => {
    changeTitle(
      "Create New Price Item",
      { title: "View Price List", path: `/price-list/${eventId}` },
      history
    );
  }, [history, eventId]);

  return (
    <>
      <Breadcrumb
        showEventName
        items={[
          { title: "Events", path: "/events" },
          { title: "Pricing", path: `/price-list/${eventId}` },
          "Create New Price Item",
        ]}
      />
      <div className="basic-form">
        <form onSubmit={formSubmitHandler}>
          <FormGroup label="Price Description *">
            <Input
              type="name"
              name={"EventItemName"}
              value={itemState.EventItemName}
              onChange={inputChangeHandler}
              placeholder="Price Description"
            />
          </FormGroup>

          <FormGroup label="Amount *">
            <Input
              type="number"
              name={"EventItemPrice"}
              value={itemState.EventItemPrice}
              onChange={inputChangeHandler}
              placeholder="Amount"
            />
          </FormGroup>
          <FormGroup>
            <FormButton disabled={isBeingProcessed}>Add Event Item</FormButton>
          </FormGroup>
        </form>
      </div>
    </>
  );
};

export default CreateEventItem;
