import React, { useEffect, useState } from "react";
import axios from "axios";
import Loader from "../../../components/UI/Loader";
import DataTable from "../../../components/Tables/DataTable";

const TicketsLogged = (props) => {
  const { orderId } = props;

  const [ticketLogIsLoading, setTicketLogIsLoading] = useState(true);
  const [ticketLogState, setTicketLogState] = useState([]);

  useEffect(() => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getticketlog/${orderId}`, { cancelToken: source.token })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setTicketLogState(convertedRes);
        console.log(convertedRes);
      })
      .finally(() => {
        setTicketLogIsLoading(false);
        source.cancel();
      });
  }, [orderId]);

  return (
    <section>
      {ticketLogIsLoading ? (
        <Loader />
      ) : (
        <DataTable
          heading="Tickets Logged"
          titles={["Name", "Log Date", "Log Quantity"]}
        >
          {ticketLogState.map((el, index) => {
            return (
              <tr key={index}>
                <td>{index + 1}</td>
                <td>{el.EventItemName}</td>
                <td>{new Date(el.TicketLogDate).toLocaleString()}</td>
                <td>{el.LogQty}</td>
              </tr>
            );
          })}
        </DataTable>
      )}
    </section>
  );
};

export default TicketsLogged;
