import React, { useEffect, useState, useRef, useCallback } from "react";
import { useLocation, NavLink } from "react-router-dom";
import $ from "jquery";
import { authLogout } from "../store/actions/authActions";
import { useDispatch } from "react-redux";

import { initNav } from "../shared/js/custom.min.js";
// import Input from "../components/FormElements/Input.js";
// import ChatBox from "./ChatBox";

const Main = (props) => {
  const { children } = props;
  const location = useLocation();
  const [, setTitle] = useState("Home");
  const [isMobile] = useState(window.matchMedia("(max-width: 768px)").matches);
  const dropDownRef = useRef(null);
  const dispatch = useDispatch();

  const logoutHandler = () => {
    dispatch(authLogout());
  };

  const dropdownToggler = useCallback(() => {
    console.log(dropDownRef.current.classList);
    dropDownRef.current.classList.toggle("show");
    console.log(dropDownRef.current.classList);
  }, []);

  const windowClickHandler = (event) => {
    if (
      !(
        event.target.matches(".dropbtn") || event.target.matches(".dropbtn img")
      )
    ) {
      var dropdowns = document.getElementsByClassName("dropdown-content");
      var i;
      for (i = 0; i < dropdowns.length; i++) {
        var openDropdown = dropdowns[i];
        if (openDropdown.classList.contains("show")) {
          openDropdown.classList.remove("show");
        }
      }
    }
  };

  useEffect(() => {
    // Close the dropdown menu if the user clicks outside of it
    window.onclick = windowClickHandler;
    return () => {
      window.removeEventListener("click", windowClickHandler);
    };
  }, []);

  useEffect(() => {
    initNav();
    $("#preloader").fadeOut(500);
    $("#main-wrapper").addClass("show");

    setTitle(() => document.title);
    $("ul.mm-active, li.mm-active").removeClass("mm-active");
    $(".mm-active").parents("ul, li").addClass("mm-active");
    $("ul.mm-active")
      .addClass("mm-show")
      .siblings("a.has-arrow.ai-icon")
      .attr("aria-expanded", true);
    return () => {
      $(".nav-control").off();
    };
  }, [location]);

  useEffect(() => {
    $("#menu a").click(function () {
      if (isMobile) {
        $(".nav-control").click();
      }
    });
    return () => {
      $("#menu a").off();
    };
  }, [isMobile]);

  return (
    <div>
      {/************************* Main wrapper start *********************/}

      <div id="main-wrapper">
        {/*********************** Nav header start *********************/}

        <div className="nav-header">
          <a href="index.html" className="brand-logo">
            {/* <img
              className="logo-abbr"
              src={require("./images/logo.png")}
              alt=""
            />
            <img
              className="logo-compact"
              src={require("./images/logo-text.png")}
              alt=""
            />
            <img
              className="brand-title"
              src={require("./images/logo-text.png")}
              alt=""
            /> */}
          </a>

          <div className="nav-control">
            <div className="hamburger">
              <span className="line"></span>
              <span className="line"></span>
              <span className="line"></span>
            </div>
          </div>

          {/************************* Nav header end *********************/}

          {/************************* Chat box start *********************/}
        </div>

        {/* <ChatBox /> */}

        {/************************* * Chat box End *******************/}

        {/************************* Header start *********************/}
        <div className="header">
          <div className="header-content">
            <nav className="navbar navbar-expand">
              <div className="collapse navbar-collapse justify-content-between">
                <div className="header-left">
                  <div className="dashboard_bar" id="dynamic-title"></div>
                </div>

                <ul className="navbar-nav header-right">
                  <li className="schedule-event-inner">
                    <button
                      id="event-list-toggler"
                      className="btn btn-primary btn-rounded"
                    >
                      LOREM
                    </button>
                  </li>
                  <li className="nav-item dropdown header-profile">
                    <button
                      className="btn dropbtn nav-link"
                      onClick={dropdownToggler}
                    >
                      <img
                        src="/ticketing-app/icons/flaticon/user.svg"
                        style={{ width: "30px" }}
                        alt="dropdown"
                      />
                    </button>
                    <div
                      id="myDropdown"
                      ref={dropDownRef}
                      style={{ top: "70%" }}
                      className="dropdown-content dropdown-menu dropdown-menu-right"
                    >
                      <button
                        className="dropdown-item ai-icon"
                        onClick={logoutHandler}
                      >
                        <svg
                          id="icon-logout"
                          xmlns="http://www.w3.org/2000/svg"
                          className="text-danger"
                          width="18"
                          height="18"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        >
                          <path d="M9 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h4"></path>
                          <polyline points="16 17 21 12 16 7"></polyline>
                          <line x1="21" y1="12" x2="9" y2="12"></line>
                        </svg>
                        <span className="ml-2">Logout </span>
                      </button>
                    </div>
                  </li>
                </ul>
              </div>
            </nav>
          </div>
        </div>

        {/************************* Header end ti-comment-alt *********************/}

        {/******************************* Sidebar start ***************************/}

        <div className="deznav">
          <div className="deznav-scroll">
            <ul className="metismenu" id="menu">
              <li>
                <NavLink
                  exact
                  to="/"
                  className="ai-icon"
                  aria-expanded="false"
                  activeClassName="mm-active"
                >
                  <i className="flaticon-381-home-3"></i>
                  <span className="nav-text">Home</span>
                </NavLink>
              </li>
              <li>
                <a
                  className="has-arrow ai-icon"
                  href="/nothing"
                  onClick={(e) => e.preventDefault()}
                  aria-expanded="false"
                >
                  <i className="flaticon-381-controls-3"></i>
                  <span className="nav-text">Events</span>
                </a>
                <ul className="mm-collapse">
                  <li>
                    <NavLink
                      to="/events"
                      className="ai-icon"
                      aria-expanded="false"
                      activeClassName="mm-active"
                    >
                      Events List
                    </NavLink>
                    <NavLink
                      to="/archived-events"
                      className="ai-icon"
                      aria-expanded="false"
                      activeClassName="mm-active"
                    >
                      Archived
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/create-event"
                      className="ai-icon"
                      aria-expanded="false"
                      activeClassName="mm-active"
                    >
                      Create New Event
                    </NavLink>
                  </li>
                </ul>
              </li>
              <li>
                <NavLink
                  activeClassName="mm-active"
                  id="ordersPageLink"
                  to="/orders/0/1"
                  className="ai-icon"
                  aria-expanded="false"
                >
                  <i className="flaticon-381-network"></i>
                  <span className="nav-text">Orders</span>
                </NavLink>
              </li>
              <li>
                <a
                  className="has-arrow ai-icon"
                  href="/nothing"
                  onClick={(e) => e.preventDefault()}
                  aria-expanded="false"
                >
                  <i className="flaticon-381-controls-3"></i>
                  <span className="nav-text">Support</span>
                </a>
                <ul className="mm-collapse">
                  <li>
                    <NavLink
                      to="/support"
                      className="ai-icon"
                      aria-expanded="false"
                      activeClassName="mm-active"
                    >
                      Support
                    </NavLink>
                    <NavLink
                      activeClassName="mm-active"
                      to="/support-list/0/1"
                      className="ai-icon"
                      aria-expanded="false"
                    >
                      Support List
                    </NavLink>
                    <NavLink
                      activeClassName="mm-active"
                      to="/create-support"
                      className="ai-icon"
                      aria-expanded="false"
                    >
                      Add Support
                    </NavLink>
                  </li>
                </ul>
              </li>
              <li>
                <NavLink
                  activeClassName="mm-active"
                  to="/ticket-details"
                  className="ai-icon"
                  aria-expanded="false"
                >
                  <i className="flaticon-381-windows"></i>
                  <span className="nav-text">Ticket Details</span>
                </NavLink>
              </li>
              <li>
                <NavLink
                  activeClassName="mm-active"
                  to="/orders/duplicate-orders/0"
                  className="ai-icon"
                  aria-expanded="false"
                >
                  <i className="flaticon-381-windows"></i>
                  <span className="nav-text">Duplicate Orders</span>
                </NavLink>
              </li>
              <h1 style={{ color: "white", margin: "auto" }}>New:</h1>
              <li>
                <NavLink
                  to="/order-sms"
                  className="ai-icon"
                  aria-expanded="false"
                  activeClassName="mm-active"
                >
                  <i className="flaticon-381-windows"></i>
                  <span className="nav-text">Order SMS</span>
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/santa-sms"
                  className="ai-icon"
                  aria-expanded="false"
                  activeClassName="mm-active"
                >
                  <i className="flaticon-381-windows"></i>
                  <span className="nav-text">Santa SMS</span>
                </NavLink>
              </li>
              <li>
                <NavLink
                  activeClassName="mm-active"
                  to="/show-bags-list"
                  className="ai-icon"
                  aria-expanded="false"
                >
                  <i className="flaticon-381-windows"></i>
                  <span className="nav-text">Show Bags List</span>
                </NavLink>
              </li>
              <li>
                <NavLink
                  activeClassName="mm-active"
                  to="/showbags-coupon-list"
                  className="ai-icon"
                  aria-expanded="false"
                >
                  <i className="flaticon-381-windows"></i>
                  <span className="nav-text">Showbags Coupon List</span>
                </NavLink>
              </li>
              <li>
                <NavLink
                  activeClassName="mm-active"
                  to="/gift-cards-list/1/1"
                  className="ai-icon"
                  aria-expanded="false"
                >
                  <i className="flaticon-381-windows"></i>
                  <span className="nav-text">Gift Cards List</span>
                </NavLink>
              </li>
              <li>
                <NavLink
                  activeClassName="mm-active"
                  to="/search-voucher"
                  className="ai-icon"
                  aria-expanded="false"
                >
                  <i className="flaticon-381-windows"></i>
                  <span className="nav-text">Search Voucher</span>
                </NavLink>
              </li>
              <li>
                <NavLink
                  activeClassName="mm-active"
                  to="/voucher-settings"
                  className="ai-icon"
                  aria-expanded="false"
                >
                  <i className="flaticon-381-windows"></i>
                  <span className="nav-text">Voucher Settings</span>
                </NavLink>
              </li>
              <li>
                <NavLink
                  activeClassName="mm-active"
                  to="/admin-view-order"
                  className="ai-icon"
                  aria-expanded="false"
                >
                  <i className="flaticon-381-windows"></i>
                  <span className="nav-text">Admin View Order</span>
                </NavLink>
              </li>
              <li>
                <NavLink
                  activeClassName="mm-active"
                  to="/admin-order"
                  className="ai-icon"
                  aria-expanded="false"
                >
                  <i className="flaticon-381-windows"></i>
                  <span className="nav-text">Admin Order</span>
                </NavLink>
              </li>
            </ul>
            {/* <div className="copyright">
              <p>Tixia Ticketing Admin Dashboard 2020 All Rights Reserved</p>
              <p className="op5">
                Made with <i className="fa fa-heart"></i> by DexignZone
              </p>
            </div> */}
          </div>
        </div>

        {/******************************* Sidebar start ***************************/}
      </div>

      <div className="content-body">
        <div className="container-fluid">
          <div className="row">
            <div className="col-12">{children}</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Main;
