import React, { useEffect } from "react";
import { useState } from "react";
import axios from "axios";

import FormButton from "../../../components/Buttons/FormButton";
import H4 from "../../../components/Headings/H4";
import FormGroup from "../../../components/FormElements/FormGroup";
import Select from "../../../components/FormElements/Select";
import Loader from "../../../components/UI/Loader";
import Input from "../../../components/FormElements/Input";

const PaymentDetails = (props) => {
  const { orderId, orderAmmount } = props;

  const [isBeingProcessed, setIsBeingProcessed] = useState(false);
  const [listIsLoading, setListIsLoading] = useState(true);
  const [refundReasonsList, setrefundReasonsList] = useState([]);
  const [refundFormState, setRefundFormState] = useState({
    OrderID: orderId,
    RefundReasonID: 0,
    RefundAmount: "",
  });
  const [refundTypeState, setRefundTypeState] = useState("full");

  const formChangeHandler = (e) => {
    const { name, value } = e.target;

    setRefundFormState((prevState) => {
      return {
        ...prevState,
        [name]: value,
      };
    });
  };

  const typeChangeHandler = (e) => {
    const { value } = e.target;

    setRefundTypeState(value);
  };

  const refundSubmitHandler = (e) => {
    e.preventDefault();
    setIsBeingProcessed(true);

    axios
      .post("/createrefund", refundFormState)
      .then((res) => {})
      .finally(() => {
        setIsBeingProcessed(false);
      });
  };

  useEffect(() => {
    let amount = 0;
    console.log(orderAmmount);
    if (refundTypeState === "full") {
      amount = orderAmmount;
    } else if (refundTypeState === "partial") {
      amount = 0;
    } else if (refundTypeState === "booking") {
      amount = 1.99;
    }

    setRefundFormState((prevState) => {
      return {
        ...prevState,
        RefundAmount: amount,
      };
    });
  }, [refundTypeState, orderAmmount]);

  useEffect(() => {
    setListIsLoading(true);
    axios
      .get("/getrefundreasonslist")
      .then((res) => {
        const convertedRes = JSON.parse(res.data);

        setrefundReasonsList(convertedRes);
      })
      .finally((res) => {
        setListIsLoading(false);
      });
  }, []);

  return (
    <section>
      <H4>Payment Details</H4>

      {listIsLoading ? (
        <Loader />
      ) : (
        <div className="basic-form">
          <form onSubmit={refundSubmitHandler}>
            <FormGroup label="Type">
              <Select
                value={refundTypeState}
                onChange={typeChangeHandler}
                firstEmpty
                options={[
                  { text: "Full Refund", value: "full" },
                  { text: "Partial Refund", value: "partial" },
                  { text: "Minus Booking Fee", value: "booking" },
                ]}
              ></Select>
            </FormGroup>
            {refundTypeState === "partial" && (
              <FormGroup label="Please enter an amount">
                <Input
                  type="text"
                  name="RefundAmount"
                  value={refundFormState.RefundAmount}
                  onChange={formChangeHandler}
                  placeholder="Amount"
                />
              </FormGroup>
            )}
            <FormGroup label="Reason">
              <Select
                value={refundFormState.RefundReasonID}
                name="RefundReasonID"
                onChange={formChangeHandler}
                firstEmpty
                options={refundReasonsList.map((el) => {
                  return {
                    text: el.RefundReason,
                    value: el.RefundReasonID,
                  };
                })}
              ></Select>
            </FormGroup>
            <FormGroup label="Amount to be refunded">
              ${refundFormState.RefundAmount}
            </FormGroup>

            <FormGroup>
              <FormButton disabled={isBeingProcessed}>
                Create a refund
              </FormButton>
            </FormGroup>
          </form>
        </div>
      )}
    </section>
  );
};

export default PaymentDetails;
