import React, { useState, Fragment, useEffect } from "react";
import { Link, useParams, useHistory } from "react-router-dom";
import axios from "axios";

import "../shared/js/custom.min.js";
import "../shared/js/deznav-init.js";
import H4 from "../components/Headings/H4.js";
import changeTitle from "../utils/change-title.js";
import DataTable from "../components/Tables/DataTable";
import Loader from "../components/UI/Loader.js";

const SupportList = () => {
  const [statuses] = useState(["Inactive", "Active", "All"]);
  const [categoryListIsLoading, setCategoryListIsLoading] = useState(true);
  const [categoryListState, setCategoryListState] = useState([]);
  const [supportListIsLoading, setsupportListIsLoading] = useState(true);
  const [supportListState, setSupportListState] = useState([]);
  const { status, category } = useParams();

  const history = useHistory();

  useEffect(() => {
    changeTitle(
      "Support List",
      { title: "Add Support", path: "/create-support" },
      history
    );
  }, [history]);

  // useEffect(() => {
  //   axios.get("/getsupportcategory").then((res) => {
  //     const convertedRes = JSON.parse(res.data);
  //     setCategoryListState(convertedRes);
  //     setCategoryListIsLoading(false);
  //     axios
  //       .post(`/getsupportlist`, {
  //         Status: status,
  //         SupportCategoryID: category,
  //       })
  //       .then((res) => {
  //         const convertedRes = JSON.parse(res.data);
  //         setSupportListState(convertedRes);
  //         setsupportListIsLoading(false);
  //       });
  //   });
  // }, [status, category]);

  useEffect(() => {
    axios.get("/getsupportcategory").then((res) => {
      const convertedRes = JSON.parse(res.data);
      setCategoryListState(convertedRes);
      setCategoryListIsLoading(false);
    });
  }, []);

  useEffect(() => {
    setsupportListIsLoading(true);
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .post(
        `/getsupportlist`,
        {
          Status: status,
          SupportCategoryID: category,
        },
        { cancelToken: source.token }
      )
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setSupportListState(convertedRes);
        setsupportListIsLoading(false);
      });
  }, [status, category]);

  return (
    <Fragment>
      <section>
        <H4>Category</H4>
        {categoryListIsLoading ? (
          <Loader />
        ) : (
          <div className="d-flex justify-content-center align-items-center my-3 flex-wrap">
            {categoryListState.map((el, index) => {
              return (
                <button
                  key={index}
                  className={`btn btn-outline-primary my-2 mx-3 ${
                    category.toString() === (index + 1).toString()
                      ? "active"
                      : ""
                  }`}
                  onClick={() =>
                    history.replace(
                      // `/proxy-comp/support-list/${status}/${el.SupportCategoryID}`
                      `/support-list/${status}/${el.SupportCategoryID}`
                    )
                  }
                >
                  {el.Name}
                </button>
              );
            })}
          </div>
        )}
      </section>

      <section>
        <H4>Status</H4>
        <div className="d-flex justify-content-center align-items-center my-3 flex-wrap">
          {statuses.map((el, index) => {
            return (
              <button
                key={index}
                className={`btn btn-outline-primary my-2 mx-3 ${
                  status.toString() === index.toString() ? "active" : ""
                }`}
                onClick={() =>
                  history.replace(
                    // `/proxy-comp/support-list/${index}/${category}`
                    `/support-list/${index}/${category}`
                  )
                }
              >
                {el}
              </button>
            );
          })}
        </div>
      </section>

      {supportListIsLoading ? (
        <Loader />
      ) : (
        <DataTable titles={["Date", "Name", "Email", "Phone", "Action"]}>
          {supportListState.map((el, index) => {
            return (
              <tr key={Math.random()}>
                <td>
                  <strong>{index + 1}</strong>
                </td>
                <td>{el.AddedDate}</td>
                <td>{el.Name}</td>
                <td>{el.Email}</td>
                <td>{el.Phone}</td>
                <td>
                  <Link
                    to={`/ticket-support/${el.SupportID}`}
                    className="btn btn-primary"
                  >
                    View
                  </Link>
                </td>
              </tr>
            );
          })}
        </DataTable>
      )}
    </Fragment>
  );
};

export default SupportList;
