import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import { useSelector } from "react-redux";

import Main from "./Layout/Main";
import Home from "./containers/Home";
import RouteWithLayout from "./containers/RouteWithLayout";
import Signup from "./containers/Signup";
import Signin from "./containers/Signin";
import Events from "./containers/Events";
import CreateEvent from "./containers/CreateEvent";
import Support from "./containers/Support";
import SupportList from "./containers/SupportList";
import CreateSupport from "./containers/CreateSupport";
import Sessions from "./containers/Sessions";
import Orders from "./containers/Orders";
import PriceList from "./containers/PriceList";
import CreateEventItem from "./containers/CreateEventItem";
import Coupons from "./containers/Coupons";
import CreateCoupon from "./containers/CreateCoupon";
import SMSLog from "./containers/SMSLog";
import TicketDetails from "./containers/TicketDetails";
import OrderDetails from "./containers/OrderDetails";
import TicketSupport from "./containers/TicketSupport";
import CreateSession from "./containers/CreateSession";
import BulkSMS from "./containers/BulkSMS";
import OrderSMS from "./containers/OrderSMS";
import SantaSMS from "./containers/SantaSMS";
import ShowBagsList from "./containers/ShowBagsList";
import CreateShowBag from "./containers/CreateShowBag";
import ShowBagCouponsList from "./containers/ShowBagCouponsList";
import GiftCardsList from "./containers/GiftCardsList";
import SearchVoucher from "./containers/SearchVoucher";
import VoucherSettings from "./containers/VoucherSettings";
import AdminViewOrder from "./containers/AdminViewOrder";
import AdminOrder from "./containers/AdminOrder/AdminOrder";
import EditEventItem from "./containers/EditEventItem";
import EditEvent from "./containers/EditEvent";
import EditCoupon from "./containers/EditCoupon";
import EditSession from "./containers/EditSession";
import BulkEmail from "./containers/BulkEmail";

const Routes = () => {
  const isAuthenticated = useSelector((state) => state.auth.isAuthenticated);

  if (isAuthenticated) {
    return (
      <Router>
        <Switch>
          <RouteWithLayout exact path="/" component={Home} layout={Main} />
          <RouteWithLayout
            exact
            path="/support"
            layout={Main}
            component={Support}
          />
          <RouteWithLayout
            exact
            path="/support-list/:status/:category"
            layout={Main}
            component={SupportList}
          />
          <RouteWithLayout
            exact
            path="/ticket-support/:supportId"
            layout={Main}
            component={TicketSupport}
          />
          <RouteWithLayout path="/events" component={Events} layout={Main} />
          <RouteWithLayout
            path="/create-event"
            component={CreateEvent}
            layout={Main}
          />
          <RouteWithLayout
            path="/edit-event"
            component={EditEvent}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/create-support"
            component={CreateSupport}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/sessions/:eventId"
            component={Sessions}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/create-session/:eventId"
            component={CreateSession}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/edit-session/:eventId"
            component={EditSession}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/orders/:sessionId/:status?"
            component={Orders}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/price-list/:eventId"
            component={PriceList}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/create-event-item/:eventId"
            component={CreateEventItem}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/edit-event-item/:eventId"
            component={EditEventItem}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/coupons/:eventId"
            component={Coupons}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/create-coupon/:eventId"
            component={CreateCoupon}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/edit-coupon"
            component={EditCoupon}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/sms-log/:sessionId/:status"
            component={SMSLog}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/ticket-details"
            component={TicketDetails}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/order-details/:orderId"
            component={OrderDetails}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/bulk-sms/:eventId/:sessionId"
            component={BulkSMS}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/bulk-email/:eventId/:sessionId"
            component={BulkEmail}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/order-sms"
            component={OrderSMS}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/santa-sms"
            component={SantaSMS}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/show-bags-list"
            component={ShowBagsList}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/create-show-bag"
            component={CreateShowBag}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/showbags-coupon-list"
            component={ShowBagCouponsList}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/gift-cards-list/:eventId/:status"
            component={GiftCardsList}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/search-voucher"
            component={SearchVoucher}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/voucher-settings"
            component={VoucherSettings}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/admin-view-order"
            component={AdminViewOrder}
            layout={Main}
          />
          <RouteWithLayout
            exact
            path="/admin-order/:orderId"
            component={AdminOrder}
            layout={Main}
          />
          <Redirect to="/" />
        </Switch>
      </Router>
    );
  } else {
    return (
      <Router>
        <Switch>
          <Route exact path="/signup" component={Signup} />
          <Route exact path="/signin" component={Signin} />
          <Redirect to="/signin" />
        </Switch>
      </Router>
    );
  }
};

export default Routes;
