import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import axios from "axios";

import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";
import Breadcrumb from "../components/UI/Breadcrumb";

import changeTitle from "../utils/change-title";

const CreateSession = () => {
  const history = useHistory();
  const { eventId } = useParams();
  const [isBeingProcessed, setisBeingProcessed] = useState(false);
  const [sessionState, setSessionState] = useState({
    EventID: eventId,
    SessionStartDateTime: "",
    SessionEndDateTime: "",
  });

  const inputChangeHandler = (e) => {
    const { name, value } = e.target;

    setSessionState((prevState) => {
      return { ...prevState, [name]: value };
    });
  };

  const formSubmitHandler = (e) => {
    e.preventDefault();
    console.log(sessionState);
    setisBeingProcessed(true);

    axios
      .post(`/createeventsession`, sessionState)
      .then((res) => {
        setSessionState({
          EventID: eventId,
          SessionStartDateTime: "",
          SessionEndDateTime: "",
        });
      })
      .finally(() => {
        setisBeingProcessed(false);
      });
  };

  useEffect(() => {
    changeTitle(
      "Create New Session",
      { title: "View Sessions", path: `/sessions/${eventId}` },
      history
    );
  }, [history, eventId]);

  return (
    <>
      <Breadcrumb
        showEventName
        items={[
          { title: "Events", path: "/events" },
          { title: "Sessions", path: `/sessions/${eventId}` },
          "Create New Session",
        ]}
      />
      <div className="basic-form">
        <form onSubmit={formSubmitHandler}>
          <FormGroup label="Start Date/Time *">
            <Input
              type="datetime-local"
              name="SessionStartDateTime"
              value={sessionState.SessionStartDateTime}
              onChange={inputChangeHandler}
            />
          </FormGroup>
          <FormGroup label="End Date/Time *">
            <Input
              type="datetime-local"
              name="SessionEndDateTime"
              value={sessionState.SessionEndDateTime}
              onChange={inputChangeHandler}
            />
          </FormGroup>
          <FormGroup>
            <FormButton disabled={isBeingProcessed}>Add Event Item</FormButton>
          </FormGroup>
        </form>
      </div>
    </>
  );
};

export default CreateSession;
