import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";

import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";
import DataTable from "../components/Tables/DataTable";
import changeTitle from "../utils/change-title";
import Loader from "../components/UI/Loader";
import H4 from "../components/Headings/H4";
import Breadcrumb from "../components/UI/Breadcrumb";

const BulkSMS = (props) => {
  const { eventId, sessionId } = useParams();
  const [formState, setFormState] = useState({
    SessionID: sessionId,
    Message: "",
  });
  const [isBeingProcessed, setIsBeingProcessed] = useState(false);
  const [ordersListIsLoading, setOrdersListIsLoading] = useState(false);
  const [ordersListState, setOrdersListState] = useState([]);

  const bulkSmsSendHandler = (e) => {
    e.preventDefault();

    setIsBeingProcessed(true);
    axios
      .post("/sendbulksms", formState)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
      })
      .finally(() => {
        setIsBeingProcessed(false);
      });
  };

  const inputChangeHandler = (e) => {
    e.preventDefault();

    setFormState((prevState) => {
      return {
        ...prevState,
        Message: e.target.value,
      };
    });
  };

  useEffect(() => {
    changeTitle("Bulk SMS");
  }, []);

  useEffect(() => {
    setOrdersListIsLoading(true);
    axios
      .post("/getorderslist", { SessionID: sessionId, Status: 1 })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setOrdersListState(convertedRes);
      })
      .finally(() => {
        setOrdersListIsLoading(false);
      });
  }, [sessionId]);

  return (
    <>
      <Breadcrumb
        showEventName
        items={[
          { title: "Events", path: `/events/${eventId}` },
          { title: "Session", path: `/sessions/${sessionId}` },

          "Bulk SMS",
        ]}
      />
      {/* <section>
        <SideBySide left="Total Active Orders:" right={} />
      <hr />
      </section> */}

      <H4>Send Bulk SMS</H4>

      <section>
        <div className="basic-form">
          <form onSubmit={bulkSmsSendHandler}>
            <FormGroup label="Message:">
              <Input
                textarea
                rows="4"
                name="Message"
                value={formState.Message}
                onChange={inputChangeHandler}
                placeholder="Type message here..."
              />
            </FormGroup>
            <FormGroup>
              <FormButton disabled={isBeingProcessed}>Send Message</FormButton>
            </FormGroup>
          </form>
        </div>
      </section>

      <hr />

      {ordersListIsLoading ? (
        <Loader />
      ) : (
        <DataTable
          heading="Orders"
          titles={["Order No", "Date", "Name", "Email", "Phone"]}
        >
          {ordersListState.map((order, index) => {
            return (
              <tr key={Math.random()}>
                <td>{index + 1}</td>
                <td>{order.OrderID}</td>
                <td>{new Date(order.OrderDate).toLocaleString()}</td>
                <td>{order.ContactName}</td>
                <td>{order.ContactEmail}</td>
                <td>{order.ContactPhone}</td>
              </tr>
            );
          })}
        </DataTable>
      )}
    </>
  );
};

export default BulkSMS;
