import React, { useEffect, useState } from "react";
import { Link, useHistory, useParams } from "react-router-dom";

import changeTitle from "../utils/change-title";
import H4 from "../components/Headings/H4";
import TableActions from "../components/Buttons/TableActions";
import DataTable from "../components/Tables/DataTable";

const GiftCardsList = () => {
  const [statuses] = useState(["Current", "Redeemed", "Expired"]);
  const { status = 0 } = useParams();
  const history = useHistory();

  useEffect(() => {
    changeTitle("Gift Cards List");
  }, []);

  return (
    <>
      <section>
        <H4>Status</H4>
        <div className="d-flex justify-content-center align-items-center my-3 flex-wrap">
          {statuses.map((el, index) => {
            return (
              <button
                key={index}
                className={`btn btn-outline-primary my-2 mx-3 ${
                  status.toString() === index.toString() ? "active" : ""
                }`}
                onClick={() =>
                  // history.replace(`/proxy-comp/gift-cards-list/${0}/${index}`)
                  history.replace(`/gift-cards-list/${0}/${index}`)
                }
              >
                {el}
              </button>
            );
          })}
        </div>
      </section>

      <DataTable
        heading={statuses[status]}
        titles={["Name", "Phone", "Balance", "Actions"]}
      >
        {[
          {
            name: "Shaikh Siddiqui",
            phone: "0430175160",
            balance: "1.00",
          },
          {
            name: "Shaikh Siddiqui",
            phone: "0430175160",
            balance: "1.00",
          },
        ].map((order, index) => {
          return (
            <tr key={Math.random()}>
              <td>{index + 1}</td>
              <td>{order.name}</td>
              <td>{order.phone}</td>
              <td>${order.balance}</td>
              <td>
                <TableActions>
                  <Link to={`/`} className="dropdown-item">
                    View Details
                  </Link>
                </TableActions>
              </td>
            </tr>
          );
        })}
      </DataTable>
    </>
  );
};

export default GiftCardsList;
