import React from "react";
import { Link, useLocation } from "react-router-dom";
import { useSelector } from "react-redux";

const Breadcrumb = (props) => {
  const { showEventName } = props;
  const { items } = props;
  const location = useLocation();
  const currentEventName = useSelector(
    (state) => state.params.currentEventName
  );

  return (
    <div className="page-titles">
      <ol className="breadcrumb">
        {items.map((el, index) => {
          return (
            <li
              key={Math.random()}
              className={`breadcrumb-item ${
                // index + 1 === items.length ? "active" : ""
                typeof el === "object" ? "" : "active"
              }`}
            >
              <Link
                to={`${typeof el === "object" ? el.path : location.pathname}`}
              >
                {`${typeof el === "object" ? el.title : el}`}
              </Link>
            </li>
          );
        })}
      </ol>
      {showEventName && (
        <div>
          <strong>{currentEventName}</strong>
        </div>
      )}
    </div>
  );
};

export default Breadcrumb;
