import React, { useEffect } from "react";
import { Link, useHistory } from "react-router-dom";

import changeTitle from "../utils/change-title";
import TableActions from "../components/Buttons/TableActions";
import DataTable from "../components/Tables/DataTable";

const ShowBagsList = () => {
  const history = useHistory();

  useEffect(() => {
    changeTitle(
      "Show Bags List",
      { title: "Add Show Bag", path: "/create-show-bag" },
      history
    );
  }, [history]);

  return (
    <>
      <section>
        <DataTable
          heading="Events"
          titles={["Image", "Description", "Price", "Actions"]}
        >
          {[
            {
              image: "/ljksd",
              description: "Lorem ipsum dolor set amet",
              price: "25.00",
            },
            {
              image: "/ljksd",
              description: "Lorem ipsum dolor set amet",
              price: "25.00",
            },
            {
              image: "/ljksd",
              description: "Lorem ipsum dolor set amet",
              price: "25.00",
            },
            {
              image: "/ljksd",
              description: "Lorem ipsum dolor set amet",
              price: "25.00",
            },
          ].map((event, index) => {
            return (
              <tr key={Math.random()}>
                <td>{index + 1}</td>
                <td>
                  <img src={event.image} alt="" />
                </td>
                <td>{event.description}</td>
                <td>${event.price}</td>
                <td>
                  <TableActions>
                    <Link
                      to={`/price-list/${event.EventID}`}
                      className="dropdown-item"
                    >
                      Delete
                    </Link>
                  </TableActions>
                </td>
              </tr>
            );
          })}
        </DataTable>
      </section>
    </>
  );
};

export default ShowBagsList;
