import React, { useEffect, useState } from "react";
import { useHistory, useLocation } from "react-router-dom";
import axios from "axios";

import changeTitle from "../utils/change-title";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";
import Select from "../components/FormElements/Select";
import Breadcrumb from "../components/UI/Breadcrumb";
import isEmpty from "../utils/is-empty";
import decodeQueryParams from "../utils/decode-query-qarams";

const EditCoupon = () => {
  const history = useHistory();
  const location = useLocation();
  const [eventId, setEventId] = useState("");
  const [couponState, setCouponState] = useState({
    EventID: eventId,
    Code: "",
    DiscountPercentage: "",
    DiscountAmount: "",
  });
  const [type, setType] = useState("");

  const inputChangeHandler = (e) => {
    const { name, value } = e.target;

    setCouponState((prevState) => {
      return {
        ...prevState,
        [name]: value,
      };
    });
  };

  const formSubmitHandler = (e) => {
    e.preventDefault();

    axios
      .post("/createeventcoupon", couponState)
      .then((res) => {})
      .catch((err) => {})
      .finally(() => {});

    console.log(couponState);
  };

  useEffect(() => {
    const { search } = location;
    const {
      EventCouponID,
      Code,
      EventDescription,
      DiscountPercentage,
      DiscountAmount,
      eventId,
    } = decodeQueryParams(search);
    setEventId(eventId);
    setCouponState({
      EventCouponID,
      Code,
      EventDescription,
      DiscountPercentage,
      DiscountAmount,
    });
    if (isEmpty(EventCouponID)) {
      history.push("/events");
    }
    if (DiscountPercentage === "0") {
      setType("Amount");
      setCouponState((prevState) => {
        return { ...prevState, DiscountPercentage: null };
      });
    } else if (DiscountAmount === "0") {
      setType("Percentage");
      setCouponState((prevState) => {
        return { ...prevState, DiscountAmount: null };
      });
    }
    changeTitle(
      "Edit Coupon",
      { title: "View Coupons List", path: `/coupons/${eventId}` },
      history
    );
  }, [history, location]);

  return (
    <>
      <Breadcrumb
        showEventName
        items={[
          { title: "Events", path: "/event" },
          { title: "Coupons", path: `/coupons/${eventId}` },
          "Edit Coupon",
        ]}
      />
      <div className="basic-form">
        <form onSubmit={formSubmitHandler}>
          <FormGroup label="Coupon Code">
            <Input
              disabled={true}
              type="text"
              name="Code"
              placeholder="Coupon Code"
              value={couponState.Code}
            />
          </FormGroup>

          <FormGroup label="Coupon type">
            <Select
              disabled={true}
              name="type"
              value={type}
              options={[
                { text: "Please select", value: "" },
                {
                  text: "Percentage",
                  value: "Percentage",
                },
                { text: "Amount", value: "Amount" },
              ]}
            ></Select>
          </FormGroup>

          {type === "Amount" && (
            <FormGroup label="Amount">
              <Input
                type="number"
                name="DiscountAmount"
                placeholder="Amount"
                value={couponState.DiscountAmount}
                onChange={inputChangeHandler}
              />
            </FormGroup>
          )}

          {type === "Percentage" && (
            <FormGroup label="Percentage">
              <Input
                type="number"
                name="DiscountPercentage"
                placeholder="Percentage"
                value={couponState.DiscountPercentage}
                onChange={inputChangeHandler}
              />
            </FormGroup>
          )}

          <FormGroup>
            <FormButton>Update</FormButton>
          </FormGroup>
        </form>
      </div>
    </>
  );
};

export default EditCoupon;
