import React from "react";

const Select = (props) => {
  const { firstEmpty, options, onOptionChange, ...rest } = props;

  return (
    <select className="form-control" {...rest}>
      {firstEmpty && <option value="">Please select</option>}
      {options.map((el) => {
        return (
          <option key={(el.value || "") + Math.random()} value={el.value}>
            {el.text}
          </option>
        );
      })}
    </select>
  );
};

export default Select;
