import React, { useEffect, useState } from "react";
import { useHistory, useLocation, useParams } from "react-router-dom";
import axios from "axios";

import decodeQueryParams from "../utils/decode-query-qarams";
import changeTitle from "../utils/change-title";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";
import Breadcrumb from "../components/UI/Breadcrumb";
import isEmpty from "../utils/is-empty";

const EditEventItem = () => {
  const history = useHistory();
  const { eventId } = useParams();
  const location = useLocation();
  const [itemState, setItemState] = useState({
    EventItemID: "",
    EventItemName: "",
    EventItemPrice: "",
  });
  const [isBeingProcessed, setIsBeingProcessed] = useState(false);

  const inputChangeHandler = (e) => {
    const { name, value } = e.target;

    setItemState((prevState) => {
      return { ...prevState, [name]: value };
    });
  };

  const formSubmitHandler = (e) => {
    e.preventDefault();
    console.log(itemState);
    setIsBeingProcessed(true);
    axios
      .post("/updateeventitem", itemState)
      .then((res) => {})
      .catch((err) => console.log(err))
      .finally(() => {
        setIsBeingProcessed(false);
      });
  };

  useEffect(() => {
    changeTitle(
      "Update Event Item",
      { title: "View Price List", path: `/price-list/${eventId}` },
      history
    );
    const { search } = location;
    const { EventItemID, EventItemName, EventItemPrice } = decodeQueryParams(
      search
    );
    setItemState({ EventItemID, EventItemName, EventItemPrice });
    if (isEmpty(EventItemID)) {
      history.push("/events");
    }
  }, [history, location, eventId]);

  return (
    <>
      <Breadcrumb
        showEventName
        items={[
          { title: "Events", path: "/events" },
          { title: "Pricing", path: `/price-list/${eventId}` },
          "Create New Event Item",
        ]}
      />
      <div className="basic-form">
        <form onSubmit={formSubmitHandler}>
          <FormGroup label="Event Item Name *">
            <Input
              type="name"
              name={"EventItemName"}
              value={itemState.EventItemName}
              onChange={inputChangeHandler}
              placeholder="Event Item Name"
            />
          </FormGroup>

          <FormGroup label="Event Item Price *">
            <Input
              type="number"
              name={"EventItemPrice"}
              value={itemState.EventItemPrice}
              onChange={inputChangeHandler}
              placeholder="Event Item Price"
            />
          </FormGroup>
          <FormGroup>
            <FormButton disabled={isBeingProcessed}>Update</FormButton>
          </FormGroup>
        </form>
      </div>
    </>
  );
};

export default EditEventItem;
