import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";

import changeTitle from "../utils/change-title";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";

const CreateShowBag = () => {
  const history = useHistory();

  useEffect(() => {
    changeTitle(
      "Add New Showbag",
      { title: "View Show Bags List", path: "/show-bags-list" },
      history
    );
  }, [history]);
  return (
    <div className="basic-form">
      <form>
        <FormGroup label="Showbag Title">
          <Input type="text" name="daterange" placeholder="Title" />
        </FormGroup>
        <FormGroup label="Showbag Price">
          <Input type="text" name="daterange" placeholder="Price" />
        </FormGroup>
        <FormGroup label="Showbag Description">
          <Input textarea rows="4" name="daterange" placeholder="Description" />
        </FormGroup>
        <FormGroup label="Showbag Image">
          <Input type="file" />
        </FormGroup>
        <FormGroup>
          <FormButton>Add New Showbag</FormButton>
        </FormGroup>
      </form>
    </div>
  );
};

export default CreateShowBag;
