import React, { useState, useCallback, useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";
import axios from "axios";
import { Link } from "react-router-dom";

import isEmpty from "../utils/is-empty";
import changeTitle from "../utils/change-title";
import BasicTable from "../components/Tables/BasicTable";
import TableActions from "../components/Buttons/TableActions";
import Loader from "../components/UI/Loader";
import Breadcrumb from "../components/UI/Breadcrumb";
import AlertSuccess from "../components/Alert/AlertSuccess";

const PriceList = () => {
  const [itemsState, setItemsState] = useState([]);
  const { eventId } = useParams();
  const [isLoading, setIsLoading] = useState(true);
  const [responseMessage, setResponseMessage] = useState("");
  const history = useHistory();

  const itemDeleteHandler = (e, EventItemID) => {
    e.preventDefault();

    setResponseMessage(false);
    axios.get(`/deleteeventitem/${EventItemID}`).then((res) => {
      if (!isEmpty(JSON.parse(res.data))) {
        setResponseMessage("Pricing deleted successfully");
      }
      fetchItems();
    });
  };

  const fetchItems = useCallback(() => {
    setIsLoading(true);
    axios.get(`/geteventitemslist/${eventId}`).then((res) => {
      const convertedRes = JSON.parse(res.data);
      setItemsState(convertedRes);
      setIsLoading(false);
    });
  }, [eventId]);

  useEffect(() => {
    fetchItems();
  }, [fetchItems]);

  useEffect(() => {
    changeTitle(
      "Price List",
      { title: "Create New Price Item", path: `/create-event-item/${eventId}` },
      history
    );
  }, [history, eventId]);

  return (
    <>
      <Breadcrumb
        showEventName
        items={[{ title: "Events", path: "/events" }, "Price List"]}
      />
      {responseMessage && <AlertSuccess>{responseMessage}</AlertSuccess>}
      {isLoading ? (
        <Loader />
      ) : (
        <BasicTable heading="Price List" titles={["Name", "Price", "Action"]}>
          {itemsState.map((item, index) => {
            return (
              <tr key={Math.random()}>
                <td>{index + 1}</td>
                <td>{item.EventItemName}</td>
                <td>${item.EventItemPrice}</td>
                <td>
                  <TableActions>
                    <Link
                      to={`/edit-event-item/${eventId}?EventItemID=${encodeURIComponent(
                        item.EventItemID
                      )}&EventItemName=${encodeURIComponent(
                        item.EventItemName
                      )}&EventItemPrice=${encodeURIComponent(
                        item.EventItemPrice
                      )}`}
                      className="dropdown-item"
                    >
                      Edit
                    </Link>
                    <button
                      onClick={(e) => itemDeleteHandler(e, item.EventItemID)}
                      className="dropdown-item"
                    >
                      Delete
                    </button>
                  </TableActions>
                </td>
              </tr>
            );
          })}
        </BasicTable>
        // <DataTable heading="Price List" titles={["Name", "Price", "Action"]}>
        //   {itemsState.map((item, index) => {
        //     return (
        //       <tr key={Math.random()}>
        //         <td>{index + 1}</td>
        //         <td>{item.EventItemName}</td>
        //         <td>{item.EventItemPrice}</td>
        //         <td>
        //           <TableActions>
        //             <Link
        //               to={`/edit-event-item?EventItemID=${encodeURIComponent(
        //                 item.EventItemID
        //               )}&EventItemName=${encodeURIComponent(
        //                 item.EventItemName
        //               )}&EventItemPrice=${encodeURIComponent(
        //                 item.EventItemPrice
        //               )}`}
        //               className="dropdown-item"
        //             >
        //               Edit
        //             </Link>
        //             <button
        //               onClick={(e) => itemDeleteHandler(e, item.EventItemID)}
        //               className="dropdown-item"
        //             >
        //               Delete
        //             </button>
        //           </TableActions>
        //         </td>
        //       </tr>
        //     );
        //   })}
        // </DataTable>
      )}
    </>
  );
};

export default PriceList;
